import React from 'react'
import ReactDOM from 'react-dom'
import * as d3 from 'd3'

// d3.select('svg')
//   .append('rect')
//   .attr('width', 300)
//   .attr('height', 300)
//   .style('fill', 'teal')

const svg = d3.select('svg')

svg
  .selectAll('circle')
  .data([5, 10, 20])
  .enter()
  .append('circle')
  .attr('r', d => d)
  .attr('cx', d => d * 5)
  .attr('cy', 50)
  .attr('fill', 'blue')

let isActive = false

svg.on('click', () => {
  d3.selectAll('circle')
    .transition()
    .duration(300)
    .attr('fill', isActive ? 'red' : 'blue')

  isActive = !isActive
})

function App() {
  return (
    <div class="container">
      <svg />
    </div>
  )
}

window.d3 = d3
